:root {
    --black: #000;
  --near-black: #111;
  --dark-gray: #333;
  --mid-gray: #555;
  --gray: #777;
  --silver: #999;
  --light-silver: #aaa;
  --moon-gray: #ccc;
  --light-gray: #eee;
  --near-white: #f8f8f8;
  --white: #fff;

  --transparent: transparent;

  --black-90: rgba(0, 0, 0, 0.9);
  --black-80: rgba(0, 0, 0, 0.8);
  --black-70: rgba(0, 0, 0, 0.7);
  --black-60: rgba(0, 0, 0, 0.6);
  --black-50: rgba(0, 0, 0, 0.5);
  --black-40: rgba(0, 0, 0, 0.4);
  --black-30: rgba(0, 0, 0, 0.3);
  --black-20: rgba(0, 0, 0, 0.2);
  --black-10: rgba(0, 0, 0, 0.1);
  --black-05: rgba(0, 0, 0, 0.05);
  --black-025: rgba(0, 0, 0, 0.025);
  --black-0125: rgba(0, 0, 0, 0.0125);

  --white-90: rgba(255, 255, 255, 0.9);
  --white-80: rgba(255, 255, 255, 0.8);
  --white-70: rgba(255, 255, 255, 0.7);
  --white-60: rgba(255, 255, 255, 0.6);
  --white-50: rgba(255, 255, 255, 0.5);
  --white-40: rgba(255, 255, 255, 0.4);
  --white-30: rgba(255, 255, 255, 0.3);
  --white-20: rgba(255, 255, 255, 0.2);
  --white-10: rgba(255, 255, 255, 0.1);
  --white-05: rgba(255, 255, 255, 0.05);
  --white-025: rgba(255, 255, 255, 0.025);
  --white-0125: rgba(255, 255, 255, 0.0125);

  --dark-red: #e7040f;
  --red: #ff4136;
  --light-red: #ff725c;
  --orange: #ff6300;
  --gold: #ffb700;
  --yellow: #ffd700;
  --light-yellow: #fbf1a9;
  --purple: #5e2ca5;
  --light-purple: #a463f2;
  --dark-purple: rebeccapurple;
  --dark-pink: #d5008f;
  --hot-pink: #ff41b4;
  --pink: #ff80cc;
  --light-pink: #ffa3d7;
  --dark-green: #137752;
  --green: #19a974;
  --light-green: #9eebcf;
  --navy: #001b44;
  --dark-blue: #00449e;
  --blue: #357edd;
  --light-blue: #96ccff;
  --lightest-blue: #cdecff;
  --washed-blue: #f6fffe;
  --washed-green: #e8fdf5;
  --washed-yellow: #fffceb;
  --washed-red: #ffdfdf;
  --left-grid-col: 220px;
  --app-header-height: 50px;
  --left-scrollable-height: 100vh;
  --center-scrollable-height: 100vh;
  --nav-area-header-bg: var(--lightest-blue);
  --nav-area-items-bg: var(--white);
  --content-area-header-bg: var(--white);
  --content-area-items-bg: var(--white);
}

body {
  margin: 0;
  background-color: var(--white);
  overflow: hidden;
}

.app-header {
  height: 50px;
  background-color: var(--white);
  border-bottom: 1px solid #ccc;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 10px;
}

.app-main {
  display: grid;
  height: clac(100vh - 50px);
  grid-template-columns: var(--left-grid-col) 1fr;
  grid-template-rows: auto 1fr;
  transition: all 0.1s ease-in-out;
}
.nav-area {
  display: grid;
  grid-template-rows: 65px 1fr;
  padding-bottom: 15px;
  height: var(--left-scrollable-height);
  overflow-y: auto;
  overflow-x: hidden;
  border-right: 1px solid #ccc;
}
.content-area {
  height: var(--center-scrollable-height);
  overflow-x: hidden;
  overflow-y: auto;
  padding: 10px;
  padding-bottom: 100px;
  display: flex;
  flex-direction: column;
}



svg {
  background-color: #fff;
  border: 1px solid #ccc;
}

path {
  shape-rendering: geometricPrecision;
}

path.merged {
  fill: none;
  stroke-width: 1;
  stroke: #222;
}

.dag path {
  fill: none;
  stroke-width: 0.5;
}

.basic-shapes path {
  fill: none;
  stroke: rebeccapurple;
  stroke-width: 1;
  shape-rendering: geometricPrecision;
}

.basic-shapes svg,
.complex svg {
  border: 0;
}

.basic-shapes path.filled {
  fill: #ddd;
  stroke-width: 0;
}

.basic-shapes a,
.complex a {
  display: block;
  margin-bottom: 10px;
}

.complex path {
  stroke-width: none;
}

p,
li {
  font-size: 16px;
  line-height: 1.4;
  margin-top: 10px;
  margin-bottom: 10px;
}

h3 {
  margin-bottom: 20px;
  padding-top: 20px;
  border-bottom: 1px solid #ddd;
}

h2 {
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 5px;
  background-color: #ddd;
}

code {
  margin: 8px;
  padding: 8px;
  background-color: #f8f8f8;
}

path {
  shape-rendering: geometricPrecision;
}

code {
  margin-left: 0;
  white-space: pre;
  display: block;
  unicode-bidi: embed;
}
